/*--- Header ---*/
.header {
  width: 100%;
  height: 55px;
  padding: 8px 14px;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  color: #eee;
  background-color: #333;
}
.header svg {
  width: 2rem;
  height: 2rem;
  margin-right: 14px;
}
.heading span {
  font-size: 1.55rem;
}
.head-logo-container {
  display: flex;
  align-items: center;
  justify-content: center;
}
/*--- Nav ---*/
.navbar {
  width: 400px;
  height: 100%;
  margin-right: 40px;
}
.nav {
  width: 100%;
  height: 100%;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.a {
  font-size: 18px;
  color: #eee;
  text-decoration: none;
}
.a:hover,
.a:focus {
  color: #ff8700;
  opacity: 0.8;
}
.active-link {
  color: #ff8700;
}
/*--- Container ---*/
.container {
  min-height: 100vh;
  padding: 5vh 0;
  background-color: #f2f3f7;
  position: relative;
}
/*--- Home ---*/
.card {
  margin: 0 auto;
  width: 60vw;
  padding: 10px 20px;
  border-radius: 20px;
  border-color: #f1f3f6;
  background-color: #f1f3f6;
  box-shadow: 6px 6px 10px rgba(0, 0, 0, 0.2), -4px -4px 10px rgba(0, 0, 0, 0.2),
    inset 4px 4px 20px rgba(255, 255, 255, 50%);
  overflow: auto;
}
canvas {
  margin: 0 auto;
}
.graph {
  margin-top: 20px;
}
.home canvas {
  padding-top: 10px;
  border-top: solid #333 1px;
  margin-top: 5px;
}
.home .location-section p:nth-child(1) {
  font-size: 2.4rem;
  line-height: 1.5;
}
.home .data-section {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}
.home .data-section .temp-data,
.home .data-section .weather-data {
  flex-grow: 1;
  height: 100%;
}
.home .data-section .temp-data {
  display: flex;
  align-items: center;
  justify-content: center;
  border-right: solid #888 1px;
}
.home .data-section .temp-data span:nth-child(1) {
  font-size: 5rem;
  line-height: 1.5;
}
.home .data-section .temp-data span:nth-child(2) {
  margin-top: -35px;
}
.home .data-section .weather-data {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  padding-left: 10%;
  font-size: 18px;
  line-height: 1.6;
}
.station-list-container {
  width: 100%;
  padding-bottom: 40px;
  font-size: 1.4rem;
  text-align: center;
}
.station-list-container select {
  padding: 4px;
  font-size: 1.3rem;
  border-radius: 5px;
  color: #333;
}
.station-list-container select:focus {
  border: solid rgba(255, 135, 0, 1) 1px;
}
.error-block {
  min-height: 250px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.error-block p {
  font-size: 2rem;
  margin-bottom: 10px;
}

/*--- Media queries ---*/
@media screen and (max-width: 1270px) {
  .card {
    width: 90vw;
  }
}
@media screen and (max-width: 720px) {
  .header {
    height: auto;
    flex-direction: column;
    line-height: 2;
  }
  .nav {
    list-style-type: disc;
    padding-left: 20px;
    flex-direction: column;
    align-items: flex-start;
  }
  .nav a {
    font-size: 1.4rem;
  }
  .graph {
    width: 95vw;
  }
  canvas {
    margin: auto;
    width: auto !important;
    height: 275px !important;
  }
}
@media screen and (max-width: 500px) {
  .home .data-section {
    flex-direction: column;
  }
  .home .data-section .temp-data {
    border: none;
  }
  .home .data-section .weather-data {
    padding-left: 0;
    line-height: 1.5;
  }
}
